import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'

Vue.use(Vuex);

const token = "041d7e693bb8afe32068d3735363e27b1f4e43d4";
/*const url = 'http://192.168.56.10:8000/core/tree/'*/
const url = "http://192.168.99.100:8000/core/tree/"

let config = {
  headers: {
    Authorization: "Token " + token
    }
}

export const store = new Vuex.Store({
  state: {
    nodes: [],
    currentNode: null
  },
  getters : {
    GET_NODES : state => {
      return state.nodes;
    }
  },
  mutations: {
    SET_NODES : (state, payload) => {
      state.nodes = payload
    },
    SET_CURRENT_NODE : (state, payload) => {
      state.currentNode = payload
    },
    ADD_NODE : (state, payload) => {
      state.nodes.push(payload)
    }
  },
  actions : {
    GET_NODE: async (context, payload) => {
      console.log(payload)
      let { data } = await Axios.get(url + payload, config)
      context.commit('SET_CURRENT_NODE', data)
    },
    SET_NODES: async (context) => {
        let { data } = await Axios.get(url, config)
        context.commit('SET_NODES', data)
    },
    SAVE_NODE : async (context, payload) => {
      Axios.post(url, payload, config).then(
        response => (context.commit('ADD_NODE', response.data)));
    },
  }
})