<template>
  <div class="container">
    <div class="sidebar">
      <el-button type="primary" icon="el-icon-plus" v-on:click="isShow = !isShow">Добавить ноду</el-button>
      <el-form v-if="isShow" :model="nodeForm" status-icon ref="ruleForm" label-width="120px" class="nodeForm">
        <el-form-item label="Name" prop="name">
          <el-input v-model="nodeForm.name" placeholder="Название"></el-input>
        </el-form-item>
        <el-form-item label="Parent" prop="parent">
          <el-select v-model="nodeForm.parent" placeholder="Родительская нода">
            <el-option v-for="item in nodesData" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="AddNode(nodeForm)">Сохранить</el-button>
        </el-form-item>
      </el-form>
      <el-table
        :data="nodesData"
        highlight-current-row
        @current-change="CurrentChange"
        style="width: 100%">
        <el-table-column
          prop="id"
          label="ID"
          width="180">
        </el-table-column>
        <el-table-column
          prop="name"
          label="Name"
          width="180">
        </el-table-column>
        <el-table-column
          prop="parent"
          label="Parent">
        </el-table-column>
      </el-table>
    </div>
    <div class="delimeter"></div>
    <div class="main">
      <div class="warn-container" v-if="currentNode==null">
        <span class="warning">Выберете ноду</span>
      </div>
      <vue-json-pretty
        v-else
        class="json-block"
        :data=currentNode>
      </vue-json-pretty>
    </div>
  </div>
</template>

<script>
import {mapActions, mapState, mapGetters} from 'vuex'
import VueJsonPretty from 'vue-json-pretty'

export default {
  name: "Nodes",
  components: {
    VueJsonPretty
  },
  data() {
    return {
      isShow: false,
      nodeForm: {
        parent: null,
        name: null
      }
    };
  },
  computed: mapState({
    nodesData: state => state.nodes,
    currentNode: state => state.currentNode
  }),
  methods: {
    ...mapActions([
      'SET_NODES',
      'GET_NODE',
      'SAVE_NODE'
    ]),
    ...mapGetters([
      'GET_NODES'
    ]),
    CurrentChange(val) {
      this.currentRow = val;
      this.GET_NODE(val.id)
    },
    AddNode(data) {
      this.SAVE_NODE(data)
    }
  },
  mounted() {
    this.SET_NODES()
  }
};
</script>

